* {
  margin: 0;
  padding: 0;
  border: 0;
  box-sizing: border-box;
  font-family: "Poppins", sans-serif !important;
}

body {
  color: #8c8c8e;
  font-size: 12px;
  font-weight: 300;
  letter-spacing: 0.5px;
  font-smooth: subpixel-antialiased;
  background: #191923;
  letter-spacing: 1.5px;
}

.App {
  margin: 2% auto;
  text-align: center;
  max-width: 1000px;
  max-height: 700px;
  background: rgba(30, 30, 40, 0.88);
  border-radius: 20px;
  box-shadow: 20px 15px 25px rgba(0, 0, 0, 0.5);
}

.progress-container {
  display: flex;
  align-items: center;
  justify-content: center;
}

.progress-bar {
  width: 250px;
  margin: 10px 30px;
  padding: 20px 0px;
  -webkit-box-shadow: 0 3px 8px 0 rgba(15, 15, 20, 0.2);
  box-shadow: 10px 10px 10px 0 rgba(15, 15, 20, 0.2);
  background: linear-gradient(159deg, #2d2d3a 0%, #2b2b35 100%);
  border-radius: 10px;
}

.progress-bar h3 {
  color: rgba(250, 250, 252, 0.5);
}

.heading1 {
  color: #fafafc;
  opacity: 0.65;
  font-size: 35px;
  padding: 20px 0px;
}

.heading4 {
  margin: 10px 0px 18px;
  font-size: 20px;
  font-weight: 600;
  color: #fafafc;
}

.heading5 {
  color: rgba(250, 250, 252, 0.85);
  font-size: 16px;
  font-weight: 600;
  margin-bottom: 10px;
}

::-webkit-scrollbar {
  width: 5px;
}

::-webkit-scrollbar-track {
  background-color: rgba(255, 255, 255, 0.75);
  border-radius: 4px;
}

::-webkit-scrollbar-thumb {
  background-color: #ffc107;
  border-radius: 5px;
}

.success {
  background-color: rgb(15, 224, 15) !important;
  color: white !important;
}

.error {
  background-color: rgb(173, 4, 4) !important;
  color: white !important;
}

